.page-body {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: normal;
  line-height: 21px;
  color: #000000;
}
.wrapper {
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .wrapper {
    width: 700px;
    margin: 0 auto;
  }
}
@media (min-width: 1280px) {
  .wrapper {
    width: 1180px;
    margin: 0 auto;
  }
}
@font-face {
  font-family: "Open Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: bold;
  font-style: bold;
  font-display: swap;
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Robotomedium.woff2") format("woff2"), url("../fonts/Robotomedium.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-weight: bold;
  font-style: bold;
  font-display: swap;
  src: url("../fonts/Robotobold.woff2") format("woff2"), url("../fonts/Robotobold.woff") format("woff");
}
.price-room {
  background-image: url("../img/bg.svg");
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  padding-top: 24px;
  padding-bottom: 49px;
  min-height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .price-room {
    padding-top: 27px;
    padding-bottom: 39px;
  }
}
.price-room-title {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 24px;
  line-height: 32px;
  font-weight: 700;
  color: #000000;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  margin-bottom: 24px;
}
@media (min-width: 768px) {
  .price-room-title {
    font-size: 36px;
    line-height: 55px;
    font-weight: 500;
  }
}
.price-room-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  row-gap: 32px;
  column-gap: 22px;
  list-style: none;
  padding: 0;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .price-room-container {
    row-gap: 37px;
    column-gap: 28px;
  }
}
@media (min-width: 1280px) {
  .price-room-container {
    row-gap: 43px;
    column-gap: 32px;
  }
}
.price-room__room {
  position: relative;
  display: flex;
  align-items: flex-end;
  background-color: #2D507C;
  width: 288px;
  min-height: 347px;
  border-radius: 13px;
  overflow: hidden;
}
.price-room__room:hover {
  box-shadow: 0 4px 15px 0 rgba(0, 0, 0, 0.6);
}
@media (min-width: 768px) {
  .price-room__room {
    width: 336px;
    min-height: 420px;
  }
}
@media (min-width: 1280px) {
  .price-room__room {
    width: 372px;
    min-height: 372px;
  }
}
.price-room__image-wrapper {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.price-room__image-wrapper::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(180deg, rgba(10, 34, 64, 0.1) 0%, #0A2240 100%);
  z-index: 1;
}
.price-room__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.price-room__info {
  display: flex;
  flex-direction: column;
  gap: 40px;
  left: 0;
  bottom: 0;
  margin: 21px;
  margin-top: 100px;
  z-index: 2;
}
@media (min-width: 768px) {
  .price-room__info {
    gap: 37px;
    margin: 24px;
    margin-top: 100px;
  }
}
.price-room__info-block {
  display: flex;
  flex-direction: column;
}
.price-room__info-block--desc {
  position: relative;
  gap: 10px;
}
.price-room__info-block--desc::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  bottom: -20px;
  left: 0;
  background-color: rgba(255, 255, 255, 0.2);
}
.price-room__info-block--price {
  gap: 16px;
  justify-content: space-between;
  align-items: stretch;
}
@media (min-width: 1280px) {
  .price-room__info-block--price {
    flex-direction: row;
    align-items: center;
    gap: 27px;
  }
}
.price-room__name-title {
  margin: 0;
}
.price-room__name-link {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 20px;
  line-height: 26px;
  font-weight: 700;
  color: #FFFFFF;
  text-decoration: none;
  margin: 0;
  padding: 0;
  cursor: pointer;
}
@media (min-width: 768px) {
  .price-room__name-link {
    font-size: 24px;
    line-height: 32px;
    font-weight: 700;
  }
}
.price-room__name-link:hover {
  text-decoration: underline;
}
.price-room__name-link:active,
.price-room__name-link:focus {
  text-decoration: none;
  opacity: 0.5;
}
.price-room__text {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #FFFFFF;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .price-room__text {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
  }
}
.price-room__price {
  font-size: 14px;
  line-height: 21px;
  font-weight: 700;
  color: #FFFFFF;
  margin: 0;
  padding: 0;
  width: 100%;
}
.price-room__price--amount {
  font-size: 20px;
  line-height: 32px;
  font-weight: 700;
  color: #FF4B77;
}
.price-room__price--currency {
  font-size: 20px;
  line-height: 32px;
  font-weight: 400;
  color: #999999;
}
@media (min-width: 768px) {
  .price-room__price {
    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
  }
  .price-room__price--amount {
    font-size: 24px;
  }
  .price-room__price--currency {
    font-size: 23px;
  }
}
.price-room__room--promo .price-room__room-promo {
  opacity: 0;
  position: absolute;
  top: 24px;
  left: 24px;
  max-width: 100%;
  background-color: #ff7647;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  padding: 4px 4px 4px 16px;
  z-index: 10;
}
.price-room__room--promo .price-room__room-promo::after {
  position: absolute;
  content: "";
  top: 0;
  right: -36px;
  height: 100%;
  width: 36px;
  background-image: url("../img/icon-sticker.svg");
  background-repeat: no-repeat;
}
.price-room__room--promo .price-room__room-promo-text {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 700;
  color: #FFFFFF;
  margin: 0;
  padding: 0;
}
.price-room__room--promo:hover .price-room__room-promo {
  opacity: 1;
}
.price-room__book-info--none {
  display: none;
}
.price-room__room--book {
  background-color: rgba(255, 255, 255, 0.4);
}
.price-room__room--book .price-room__room-promo {
  display: none;
}
.price-room__room--book::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-image: none;
  background-color: rgba(255, 255, 255, 0.4);
  z-index: 3;
}
.price-room__room--book .price-room__book-info--none {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 153px;
  width: 100%;
  background-color: #3C3E43;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  text-align: center;
  color: #FFFFFF;
  z-index: 4;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .price-room__room--book .price-room__book-info--none {
    height: 162px;
  }
}
@media (min-width: 1280px) {
  .price-room__room--book .price-room__book-info--none {
    height: 98px;
  }
}
.price-room__room--book .price-room__book-link {
  color: #FF4B77;
  text-decoration: none;
  cursor: pointer;
}
.price-room__room--book .price-room__book-link:hover {
  text-decoration: underline;
}
.price-room__room--book .price-room__book-link:active,
.price-room__room--book .price-room__book-link:focus {
  text-decoration: none;
  color: #FF97B0;
}
.price-room__book-button {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  text-align: center;
  color: #FFFFFF;
  background-color: #F91155;
  border: none;
  border-radius: 3px;
  width: 100%;
  padding: 8px 24px 10px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .price-room__book-button {
    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
    border-radius: 5px;
    padding: 11px 24px 13px;
  }
}
@media (min-width: 1280px) {
  .price-room__book-button {
    width: auto;
  }
}
.price-room__book-button:hover {
  background-color: #D8003F;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}
.price-room__book-button:active,
.price-room__book-button:focus {
  color: #F9637C;
  background-color: #D8003F;
  box-shadow: none;
}
