.price-room__room--promo {
    
    .price-room__room-promo {
        opacity: 0;
        position: absolute;
        top: 24px;
        left: 24px;
        max-width: 100%;
        background-color: @promo-sticker;
        border-top-left-radius: 3px;
        border-bottom-left-radius: 3px;
        padding: 4px 4px 4px 16px;
        z-index: 10;

        &::after {
            position: absolute;
            content: "";
            top: 0;
            right: -36px;
            height: 100%;
            width: 36px;
            background-image: url("../img/icon-sticker.svg");
            background-repeat: no-repeat;
        }
    }

    .price-room__room-promo-text {
        font-family: "Roboto", "Arial", sans-serif;
        .font-style(14px; 21px; 700);
        color: @white;
        margin: 0;
        padding: 0;
    }
}

.price-room__room--promo:hover {

    .price-room__room-promo {
        opacity: 1;
        
    }
}
