.page-body {
    width: 100%;
    margin: 0;
    padding: 0;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    line-height: 21px;
    color: @black;
}

.wrapper {
    width: 280px;
    margin: 0 auto;

    @media (min-width:@tablet-size) {
        width: 700px;
        margin: 0 auto;
    }

    @media (min-width:@desktop-size) {
        width: 1180px;
        margin: 0 auto;
    }
}

.font-style(@font-size; @line-height; @font-weight) {
    font-size: @font-size;
    line-height: @line-height;
    font-weight: @font-weight;
}
