.price-room__book-button {
    font-family: "Open Sans", "Arial", sans-serif;
    .font-style(14px; 24px; 700);
    text-align: center;
    color: @white;
    background-color: @pink-button;
    border: none;
    border-radius: 3px;
    width: 100%;
    padding: 8px 24px 10px;
    cursor: pointer;

    @media (min-width:@tablet-size) {
        .font-style(16px; 24px; 700);
        border-radius: 5px;
        padding: 11px 24px 13px;
    }

    @media (min-width:@desktop-size) {
        width: auto;
    }

    &:hover {
        background-color: @pink-button-special-bg;
        box-shadow: 0 4px 4px 0 fade(@black, 25%);
    }

    &:active,
    &:focus {
        color: @pink-button-special-text;
        background-color: @pink-button-special-bg;
        box-shadow: none;
    }
}
