.price-room__book-info--none {
    display: none;
}

.price-room__room--book {
    background-color: fade(@white, 40%);

    .price-room__room-promo {
        display: none;
    }

    &::after {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-image: none;
        background-color: fade(@white, 40%);
        z-index: 3;
    }

    .price-room__book-info--none {
        position: absolute;
        bottom: 0;
        left: 0;
        height: 153px;
        width: 100%;
        background-color: @grey-book-bg;
        .font-style(14px; 21px; 400);
        text-align: center;
        color: @white;
        z-index: 4;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;

        @media (min-width:@tablet-size) {
            height: 162px;
        }

        @media (min-width:@desktop-size) {
            height: 98px;
        }
    }

    .price-room__book-link {
        color: @pink-standart;
        text-decoration: none;
        cursor: pointer;
        
        &:hover {
            text-decoration: underline;
        }
        &:active,
        &:focus {
            text-decoration: none;
            color: @pink-light;
        }
    }
}
